body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.title{
  text-align: center;
  /* text-decoration: underline; */
  color: purple;
  font-size: 2rem;
}
.section-container{
  text-align: center;
  width: 70vw;
  display: flex;
  flex-direction: column;
  /* border: 1px solid black; */
  border-radius: 3px;
  margin: 1rem;
  padding: .5rem 1rem 1rem;
  box-shadow: 1px 1px 5px grey;
}
.grocery-container{
  /* display: flex;
  align-items: center;
  justify-content: space-between; */
  /* border: 1px solid black; */
  padding: .5rem;
}
.clear-btn{
  border: none;
  background-color: transparent;
  color: red;
  font-size: .8rem;
  font-weight: 700;
  border-radius: 5px;
  /* border: 1px solid red; */
  height: 1.5rem;
  box-shadow: 1px 1px 1px rgb(247, 116, 116), inset 1px 1px 1px rgb(248, 180, 180);
}
.clear-btn:hover{
  cursor: pointer;
}
.clear-btn:active{
  
  transform: scale(.99);
  box-shadow: 1px 1px 1px rgb(248, 180, 180), inset 1px 1px 1px rgb(247, 116, 116);  
  
}
/* .list-container{
  border: 1px solid black;
  border-radius: 10px;
  padding: 0 1rem;
  box-shadow: 1px 1px 5px grey;
} */
input{
  outline: none;
  border: 1px solid rgb(192, 190, 190);
  margin-right: .5rem;
  padding: .5rem;
  border-radius: 5px;
  width: 60%;
}
.submit-btn{
  outline: none;
  border: none;
  background-color: lightgreen;
  color: green;
  font-size: 1rem;
  font-weight: 700;
  border-radius: 5px;
  /* border: 1px solid red; */
  height: 2rem;
  padding: 0 1rem;
  
}
.submit-btn:hover{
  cursor: pointer;
}
.submit-btn:active{  
  transform: scale(.98);  
}
.grocery-item{
  /* border: 1px solid black; */
  border-bottom: 1px solid black;
  padding: .5rem 1rem 0;
  border-radius: 5px;
  margin: .5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.grocery-title{
  text-align: center;  
  color: blueviolet;
  font-weight: 600;
  font-size: 1.2rem;
}
.btn-container{
  display: flex;
  justify-content: center;
  align-items: center;
}
.edit-btn, .delete-btn{
  outline: none;
  border: none;
  background-color: transparent;
}
.edit-btn{
  color: rgb(25, 171, 25);
}
.delete-btn{
  color: red;
}
.alert{
  height: 2rem !important;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.alert-msg{
  font-size: 1rem;
  padding-top: 1rem;
}